//导入文件
import http from '../httpConfig'

// /api/importData/current-import-data/v1 通用导入
// /api/importData/user-import-data/v1 用户信息导入
// /api/importData/org-import-data/v1 组织机构导入

//上传文件  通用导入
export const uploadFileAll = (params = {}) => {
  return new Promise((resolve, reject) => {
    http.uploadFile('/api/importData/current-import-data/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}

